<template>
    <Dialog v-model="show" :options="{
        title: __('Send Mail'),
        size: '4xl',
        actions: [{
            label: __('Send'),
            variant: 'solid',
            onClick: (close) => {
                send(close)
            }
        }]
    }">
        <template #body-content>
            <TextEditor
                ref="textEditor"
                :editor-class="[
                    'prose-sm max-w-none',
                    editable && 'min-h-[7rem]',
                    '[&_p.reply-to-content]:hidden',
                ]"
                :content="mail.html"
                @change="(val) => (mail.html = val)"
                :starterkit-options="{
                    heading: { levels: [2, 3, 4, 5, 6] },
                    paragraph: false,
                }"
                :placeholder="placeholder"
                :editable="editable"
                :extensions="[CustomParagraph]"
            >
                <template #top>
                    <div class="flex flex-col gap-3">
                        <div class="sm:mx-10 mx-4 flex items-center gap-2 border-t pt-2.5">
                            <span class="text-xs text-gray-500">{{ __('To') }}:</span>
                            <MultiselectInput
                                class="flex-1"
                                v-model="mail.to"
                                :validate="validateEmail"
                                :error-message="
                                (value) => __('{0} is an invalid email address', [value])
                                "
                            />
                            <div class="flex gap-1.5">
                                <Button
                                    :label="__('Cc')"
                                    variant="ghost"
                                    @click="toggleCC()"
                                    :class="[
                                        cc ? '!bg-gray-300 hover:bg-gray-200' : '!text-gray-500',
                                    ]"
                                />
                                <Button
                                    :label="__('Bcc')"
                                    variant="ghost"
                                    @click="toggleBCC()"
                                    :class="[
                                        bcc ? '!bg-gray-300 hover:bg-gray-200' : '!text-gray-500',
                                    ]"
                                />
                            </div>
                        </div>
                        <div v-if="cc" class="sm:mx-10 mx-4 flex items-center gap-2">
                            <span class="text-xs text-gray-500">{{ __('Cc') }}:</span>
                            <MultiselectInput
                                ref="ccInput"
                                class="flex-1"
                                v-model="mail.cc"
                                :validate="validateEmail"
                                :error-message="
                                (value) => __('{0} is an invalid email address', [value])
                                "
                            />
                        </div>
                        <div v-if="bcc" class="sm:mx-10 mx-4 flex items-center gap-2">
                            <span class="text-xs text-gray-500">{{ __('Bcc') }}:</span>
                            <MultiselectInput
                                ref="bccInput"
                                class="flex-1"
                                v-model="mail.bcc"
                                :validate="validateEmail"
                                :error-message="
                                (value) => __('{0} is an invalid email address', [value])
                                "
                            />
                        </div>
                        <div class="sm:mx-10 mx-4 flex items-center gap-2 pb-2.5">
                            <span class="text-xs text-gray-500">{{ __('Subject') }}:</span>
                            <TextInput
                                class="flex-1 border-none bg-white hover:bg-white focus:border-none focus:!shadow-none focus-visible:!ring-0"
                                v-model="subject"
                            />
                        </div>
                    </div>
                </template>
                <template v-slot:editor="{ editor }">
                    <EditorContent
                        :class="[
                        editable &&
                            'sm:mx-10 mx-4 max-h-[35vh] overflow-y-auto border-t py-3',
                        ]"
                        :editor="editor"
                    />
                </template>
                <template v-slot:bottom>
                    <div v-if="editable" class="flex flex-col gap-2">
                        <div class="flex flex-wrap gap-2 sm:px-10 px-4">
                            <AttachmentItem
                                v-for="a in attachments"
                                :key="a.file_url"
                                :label="a.file_name"
                            >
                                <template #suffix>
                                <FeatherIcon
                                    class="h-3.5"
                                    name="x"
                                    @click.stop="removeAttachment(a)"
                                />
                                </template>
                            </AttachmentItem>
                        </div>
                        <div
                        class="flex justify-between gap-2 overflow-hidden border-t sm:px-10 px-4 py-2.5"
                        >
                            <div class="flex gap-1 items-center overflow-x-auto">
                                <TextEditorBubbleMenu :buttons="textEditorMenuButtons" />
                                <EmojiPicker
                                    v-model="emoji"
                                    v-slot="{ togglePopover }"
                                    @update:modelValue="() => appendEmoji()"
                                >
                                    <Button variant="ghost" @click="togglePopover()">
                                        <template #icon>
                                            <SmileIcon class="h-4" />
                                        </template>
                                    </Button>
                                </EmojiPicker>
                                <FileUploader
                                    :upload-args="{
                                        doctype: doctype,
                                        docname: modelValue.name,
                                        private: true,
                                    }"
                                    @success="(f) => attachments.push(f)"
                                >
                                    <template #default="{ openFileSelector }">
                                        <Button variant="ghost" @click="openFileSelector()">
                                            <template #icon>
                                                <Paperclip class="h-4" />
                                            </template>
                                        </Button>
                                    </template>
                                </FileUploader>
                            </div>
                            <div class="mt-2 flex items-center justify-end space-x-2 sm:mt-0">
                                <Button v-bind="discardButtonProps || {}" :label="__('Discard')" />
                                <Button
                                    variant="solid"
                                    v-bind="submitButtonProps || {}"
                                    :label="__('Send')"
                                />
                            </div>
                        </div>
                    </div>
                </template>
            </TextEditor>
        </template>
    </Dialog>
</template>
<script setup>
import { Dialog, TextEditor, createResource, FileUploader } from "frappe-ui";
import { reactive, watch, inject } from "vue";
import { EditorContent } from '@tiptap/vue-3'
import { Paperclip } from "lucide-vue-next"
import EmojiPicker from "@/components/EmojiPicker.vue";

const user = inject("$user");
const attachments = defineModel('attachments')
const show = defineModel()

const props = defineProps({
    replyDetails: {
        type: Object,
        required: false
    }
})

const mail = reactive({
    to: "",
    cc: "",
    bcc: "",
    subject: "",
    html: ""
})

watch(show, () => {
    if (show.value && props.replyDetails) {
        mail.to = props.replyDetails.to
        mail.cc = props.replyDetails.cc
        mail.bcc = props.replyDetails.bcc
        mail.subject = props.replyDetails.subject
        mail.html = props.replyDetails.html
        mail.reply_to_mail_type = props.replyDetails.reply_to_mail_type
        mail.reply_to_mail_name = props.replyDetails.reply_to_mail_name
    }
})

const sendMail = createResource({
    url: "mail.api.outbound.send",
    method: "POST",
    makeParams(values) {
        return {
            from_: `${user.data?.full_name} <${user.data?.name}>`,
            ...mail
        }
    },
})

const send = (close) => {
    sendMail.submit({}, {
        onSuccess() {
            close()
        }
    })
}

const toggleCC = () => {
  cc.value = !cc.value
  cc.value && nextTick(() => ccInput.value.setFocus())
}

const toggleBCC = () => {
  bcc.value = !bcc.value
  bcc.value && nextTick(() => bccInput.value.setFocus())
}

</script>